import React from "react";
import styled from "styled-components";
import { Link } from "gatsby";

const BlogListContainer = styled.div`
  min-width: 1024px;
  min-height: 600px;
`;

const BlogListContent = styled.div`
  display: flex;
  flex-direction: column;
`;

const BlogPagination = styled.div``;

const BlogPost = styled.div`
  display: flex;
  justify-content: space-between;
`;

const BlogTitle = styled.span``;

const BlogDate = styled.span``;

export default function BlogListComponent(props) {
  const pagination = props.pagination;
  return (
    <BlogListContainer>
      <BlogListContent>
        {props.posts.map(({ node }, index) => {
          const title = node.frontmatter.title || node.fields.slug;
          return (
            <Link key={node.fields.slug} to={node.frontmatter.path}>
              <BlogPost>
                <BlogTitle>{`${title}`}</BlogTitle>
                <BlogDate>{node.frontmatter.date}</BlogDate>
              </BlogPost>
            </Link>
          );
        })}
      </BlogListContent>
      <BlogPagination>
        {pagination
          ? pagination.numPages > 1
            ? Array(2)
                .fill(0)
                .map((e, idx) => (
                  <Link
                    key={idx + 1}
                    to={idx === 0 ? `/blog` : `/blog/${idx + 1}`}
                  >
                    {idx + 1}
                  </Link>
                ))
            : null
          : null}
      </BlogPagination>
    </BlogListContainer>
  );
}
